import React, { Component } from 'react'
import { array } from 'prop-types'

export default class FloorPlan extends Component {
  static propTypes = {
    floorPlan: array,
  }
  render() {
    const { floorPlan } = this.props
    return (
      <div className="cls-floorplan-wrapper mb-4">
        <h3 className="cls-header3 mb-4">Explore Floor Plan</h3>
        <div className="row">
          {floorPlan.map((item, i) => (
            <div className="col-12 col-sm-6 mb-4" key={i}>
              <h5>{item.title}</h5>
              <div>
                <a href={item.image} target="_blank">
                  <img
                    src={item.image}
                    className="img-fluid"
                    width={310}
                    height={310}
                  />
                </a>
              </div>
            </div>
          ))}
        </div>
      </div>
    )
  }
}



