import React, { Component } from 'react'
import { array } from 'prop-types'

export default class ShowRooms extends Component {
  static propTypes = {
    showRooms: array,
  }
  render() {
    const { showRooms } = this.props
    return (
      <div className="cls-floorplan-wrapper mb-4">
        <h3 className="cls-header3 mb-4">Explore Environment</h3>
        <div className="row">
          {showRooms.map((item, i) => (
            <div className="col-12 col-sm-6 mb-4" key={i}>
              <div>
                <a href={item} target="_blank">
                  <img
                    src={item}
                    style={{ height: '200px', width: '100%' }}
                    className="show-room"
                  />
                </a>
              </div>
            </div>
          ))}
        </div>
      </div>
    )
  }
}



