import React, { Component } from 'react'

// Link
import { Link } from 'react-router-dom'

export default class Footer extends Component {
  render() {
    return (
      <section id="sec-bottom">
        <div className="container">
          <div className="row">
            <div className="col-12 col-sm-3">
              <div>
                <img
                  src={require('../../assets/images/logi.png')}
                  className="img-fluid"
                />
              </div>
              {/*<div className="cls-slogan">Malaysia Property Insight</div>*/}
            </div>

            <div className="col-12 col-sm-3">
              <h4 className="cls-header4 mb-3">Quick Link</h4>
              <ul className="cls-quicklink-list">
                <li>
                  <Link to="/about-us">About Us</Link>
                </li>
                <li>
                  <Link to="/all-property">All Property</Link>
                </li>
                <li>
                  <Link to="/contact-us">Contact Us</Link>
                </li>
              </ul>
            </div>

            <div className="col-12 col-sm-3">
              <h4 className="cls-header4 mb-3">Address</h4>
              <div className="cls-address-info">
                2-1-41, One Square, <br /> Tingkat Mahsuri 1, <br /> 11900 Bayan
                Lepas, <br />Pulau Pinang.
              </div>
            </div>

            <div className="col-12 col-sm-3">
              <h4 className="cls-header4 mb-3">Contact Info</h4>
              <div className="cls-bottom-info">
                <div className="cls-bottom-email">
                  <i className="far fa-envelope" />&nbsp;{' '}
                  <a href="mailto:weiloongchew94@gmail.com">
                    weiloongchew94@gmail.com
                  </a>
                </div>
                <div className="cls-bottom-email">
                  <i className="far fa-envelope" />&nbsp;{' '}
                  <a href="mailto:chewtuckwai@gmail.com">
                    chewtuckwai@gmail.com
                  </a>
                </div>
                <div className="cls-bottom-phone">
                  <i className="fas fa-phone" />&nbsp;<a href="tel:+60164637218">
                    +60 16463 7218
                  </a>
                </div>
                <div className="cls-bottom-phone">
                  <i className="fas fa-phone" />&nbsp;<a href="tel:+60125484854">
                    +60 12548 4854
                  </a>
                </div>
                {/*<div className="cls-bottom-fax">
                  <i className="fas fa-fax" />&nbsp;<a href="tel:+6012345678">
                    +60 1234 5678
                  </a>
                </div>*/}
              </div>
            </div>
          </div>
          <hr />
          <div className="row">
            <div className="col-12 col-sm-6">@2018 CopyRight</div>
            <div className="col-12 col-sm-6 cls-txt-right">
              <ul className="cls-socialmedia">
                <li>
                  <a
                    href="https://www.facebook.com/MalaysiaPropertyInsight155/"
                    target="_blank"
                  >
                    <i className="fab fa-facebook-f" />
                  </a>
                </li>
                {/* <li>
                  <a href="#" target="_blank">
                    <i className="fab fa-twitter" />
                  </a>
                </li>*/}
              </ul>
            </div>
          </div>
          <div className="cls-vertical-line" />
        </div>
      </section>
    )
  }
}



