import React, { Component } from 'react'

// Component
import {
  BannerDetails,
  ContactAgent,
  Download,
  Facilities,
  FloorPlan,
  GoogleMap,
  Overview,
  PropertyBanner,
  RequestVisit,
  VideoOverview,
} from './detailComponents'

// Const
import { propertyDetails } from '../../data/const'
import ShowRooms from './detailComponents/ShowRooms'

export default class Details extends Component {
  render() {
    const { location } = this.props
    const { pathname } = location
    const propertyName = pathname.split('/')[2]

    return (
      <div>
        {/** Detail Banner **/}
        <PropertyBanner link={pathname} />
        {/** Banner Details**/}
        <BannerDetails banner={propertyDetails[propertyName].banner} />

        <section id="sec-detail">
          <div className="container">
            <div className="row">
              {/** Property Details **/}
              <div className="col-12 col-sm-8">
                {/** Overview **/}
                <Overview overview={propertyDetails[propertyName].overview} />
                {/** Facilities **/}
                <Facilities
                  facilities={propertyDetails[propertyName].facilities}
                />
                {/** Video Overview **/}
                {/*<VideoOverview />*/}
                {/** Show Rooms **/}
                <ShowRooms
                  showRooms={propertyDetails[propertyName].showRooms}
                />
                {/** Floor Plan **/}
                <FloorPlan
                  floorPlan={propertyDetails[propertyName].floorPlan}
                />
              </div>
              {/** End of Property Details **/}

              {/** Contact Details **/}
              <div className="col-12 col-sm-4">
                {/** Brochure Download **/}
                <Download pathname={propertyName} />
                {/** Google Map **/}
                {/*<GoogleMap />*/}
                {/** Contact Agent **/}
                <ContactAgent
                  name="Chew Wei Loong"
                  message="Bachelor of Computer Science (HONS), Ren Tag: 27540"
                  mobile="+60 16463 7218"
                  email="weiloongchew94@gmail.com"
                  image={require('../../assets/images/agent-img.jpg')}
                />
                {/** Contact Agent **/}
                <ContactAgent
                  name="Chew Tuck Wai"
                  message="Bachelor of Business Administration (HONS), Ren Tag: 25381"
                  mobile="+60 12548 4854"
                  email="chewtuckwai@gmail.com"
                  image={require('../../assets/images/agent-img1.png')}
                />
                {/** Request Visit **/}
                <RequestVisit />
              </div>
              {/** End of Contact Details**/}
            </div>
          </div>
        </section>
      </div>
    )
  }
}



