import React, { Component } from 'react'
import { string, array } from 'prop-types'

// Link
import { Link } from 'react-router-dom'

export default class PropertyCard extends Component {
  static propTypes = {
    link: string,
    image: string,
    name: string,
    price: string,
    details: array,
  }
  render() {
    const { link, image, name, details, price } = this.props
    return (
      <div className="col-12 col-sm-6 mb-4">
        <div className="property-card">
          <div className="property-image">
            <Link to={link}>
              <img src={image} alt={name} />
            </Link>
          </div>
          <div className="property-detail" align="left">
            <span className="property-category">Commercial, sell</span>
            <h3>{name}</h3>
            <span className="property-price">{price}/Sqft</span>
            <span className="property-summary mb-2 mt-2">
              <ul>{details.map((val, i) => <li key={i}>{val}</li>)}</ul>
            </span>
            <span className="all-btn">
              <Link to={link} role="button">
                More Info
              </Link>
            </span>
          </div>
        </div>
      </div>
    )
  }
}



