import React, { Component } from 'react'

export default class HomeBookAppointment extends Component {
  render() {
    return (
      <section id="book-section">
        <div className="container" align="middle">
          <div className="book-title">
            <h3>Book Your Appointment</h3>
            <div className="book-subtitle">
              <span
                style={{ fontSize: '60px' }}
                data-mce-style="font-size: 60px;"
              >
                Call Us Now: <br />+60 16463 7218, +60 12548 4854
              </span>
            </div>
          </div>
        </div>
      </section>
    )
  }
}



