import React, { Component } from 'react'

// Components
import { AllDetails, AllSearch, AllBanner } from './allComponents'

export default class AllProperty extends Component {
  render() {
    return (
      <div>
        {/** All Banner **/}
        <AllBanner />
        {/** All Search **/}
        {/*<AllSearch />*/}
        {/** All Details **/}
        <AllDetails />
      </div>
    )
  }
}



