import React, { Component } from 'react'
import { object } from 'prop-types'

export default class BannerDetails extends Component {
  static propTypes = {
    banner: object,
  }
  render() {
    const { banner } = this.props
    return (
      <section id="sec-info">
        <div className="container">
          <div className="row">
            <div className="col-12 col-sm-8">
              <div className="cls-detail-wrapper">
                <div className="cls-detail-name">{banner.title}</div>
                <div className="cls-detail-type">
                  <i className="fa fa-map-marker-alt" aria-hidden="true" />{' '}
                  {banner.subtitle}{' '}
                  <span className="cls-icon-type">
                    <i className="fas fa-bookmark" /> {banner.type}
                  </span>
                </div>
              </div>
            </div>
            <div className="col-12 col-sm-4 cls-txt-right">
              <div className="cls-price">
                <span className="cls-txt-price">RM</span> {banner.price}
              </div>
              {/*<div className="cls-per-sqft">RM{banner.sqft}/sqft</div>*/}
            </div>
          </div>
        </div>
      </section>
    )
  }
}



