import React, { Component } from 'react'

// Components
import { AboutBanner, AboutDetails } from './aboutComponents'

export default class About extends Component {
  render() {
    return (
      <div>
        {/** About Banner **/}
        <AboutBanner />
        {/** About Details **/}
        <AboutDetails />
      </div>
    )
  }
}



