import React, { Component } from 'react'

export default class AllSearch extends Component {
  render() {
    return (
      <section id="search-section">
        <div className="container" align="middle">
          <div className="cls-search-icon ">
            <i className="fas fa-search fa-2x" />
          </div>
          <h4 className="search-title">SEARCH YOUR PROPERTIES</h4>
          <form>
            <div className="form-row">
              <div className="col-12 col-sm-3 mb-2">
                <input
                  type="text"
                  name="keyword"
                  id="keyword"
                  placeholder="Keyword"
                  className="form-control"
                />
              </div>
              <div className="col-12 col-sm-3 mb-2">
                <select name="status" id="gdg" className="form-control">
                  <option value selected="selected">
                    Location
                  </option>
                  <option value="California">California</option>
                  <option value="New York">New York</option>
                  <option value="Grand Rapids">Grand Rapids</option>
                  <option value="Eastern Province">Eastern Province</option>
                </select>
              </div>
              <div className="col-12 col-sm-3 mb-2">
                <select name="status" id="status" className="form-control">
                  <option value selected="selected">
                    Location
                  </option>
                  <option value="California">California</option>
                  <option value="New York">New York</option>
                  <option value="Grand Rapids">Grand Rapids</option>
                  <option value="Eastern Province">Eastern Province</option>
                </select>
              </div>
              <div className="col-12 col-sm-3 mb-2">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Last name"
                />
              </div>
            </div>
          </form>
        </div>
      </section>
    )
  }
}



