import React, { Component } from 'react'

export default class GoogleMap extends Component {
  render() {
    return (
      <div className="cls-map-wrapper">
        <div id="map" />
        <div className="cls-map-content-wrapper">
          <div className="cls-map-icon">
            <span className="cls-txt-icon">
              <i className="fa fa-map-marker-alt fa-2x" aria-hidden="true" />
            </span>
          </div>
          <h3 className="cls-header3 mt-3">Address</h3>
          <div className="cls-txt-address">
            247-269 Golf Club Rd, Pleasant Hill, CA 94523, USA
          </div>
        </div>
      </div>
    )
  }
}



