import React, { Component } from 'react'

//Const
import { advantage } from '../../../data/const'

export default class HomeAdvantages extends Component {
  render() {
    return (
      <section
        id="advantages-section"
        className="sppb-section  empire-property-search"
      >
        <div className="container">
          <div className="advantages-wrapper">
            <div className="advantages-detail" align="middle">
              <h2>Our Advantages</h2>
              <p>
                Quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea
                commodo consequat. Duis aute irure dolor in<br />
                reprehenderit in voluptate velit esse cillum dolore eu fugiat
                nulla pariatur.
              </p>
            </div>
            <div className="row mt-5">
              {advantage.map((item, i) => (
                <div className="col-12 col-sm-4 mb-2" align="middle" key={i}>
                  <div className="advantages-content">
                    <h3>
                      <span>{item.title}</span> <br />
                      {item.subTitle}
                    </h3>
                    <div>
                      {item.detailOne}
                      <br />
                      {item.detailTwo}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>
    )
  }
}



