import React, { Component } from 'react'
import { array } from 'prop-types'

export default class Facilities extends Component {
  static propTypes = {
    facilities: array,
  }
  render() {
    const { facilities } = this.props
    return (
      <div className="cls-feature-wrapper mb-4">
        <h3 className="cls-header3 mb-4">Facilities</h3>
        <div className="row">
          {facilities.map((item, i) => (
            <div className="col-12 col-sm-4 mb-3" key={i}>
              <span style={{ fontWeight: 'bold' }}>{item.theme}</span>
              <ul className="cls-facility-list">
                {item.details.map((val, i) => <li key={i}>{val}</li>)}
              </ul>
            </div>
          ))}
        </div>
      </div>
    )
  }
}



