import React, { Component } from 'react'

// Link
import { Link } from 'react-router-dom'

export default class ContactBanner extends Component {
  render() {
    return (
      <section id="sec-banner">
        <div className="container">
          <div className="cls-banner-wrapper text-center">
            <h2 className="cls-header2">Contact Us</h2>
            <div className="cls-breadcrumb">
              <ul className="cls-breadcrumb-list">
                <li>
                  <Link to="/">
                    <i className="fas fa-home" />{' '}
                  </Link>
                </li>
                /
                <li>
                  <Link to="/contact-us"> Contact Us</Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
    )
  }
}



