import React, { Component } from 'react'

export default class ContactForm extends Component {
  render() {
    return (
      <section id="sec-contact-form">
        <div className="container">
          <h3 className="cls-header3 mb-3">Contact Form</h3>
          <form>
            <div className="form-row mb-3">
              <div className="col">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Full Name"
                />
              </div>
              <div className="col">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Email"
                />
              </div>
              <div className="col">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Contact Number"
                />
              </div>
            </div>
            <div className="form-row mb-3">
              <div className="col">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Subject"
                />
              </div>
            </div>
            <div className="form-row mb-3">
              <div className="col">
                <textarea
                  type="text"
                  className="form-control"
                  placeholder="Message"
                  rows="5"
                />
              </div>
            </div>
            <button type="submit" className="btn btn-primary mb-2">
              Submit
            </button>
          </form>
        </div>
      </section>
    )
  }
}



