import React, { Component } from 'react'

export default class SideBar extends Component {
  render() {
    return (
      <nav id="sidebar-m">
        <div id="dismiss">
          <i className="fas fa-window-close fa-2x" />
        </div>
        <ul className="list-unstyled components cls-submenu-m">
          <li className="nav-item active">
            <a className="nav-link" href="index.html">
              Home
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="about.html">
              About
            </a>
          </li>
          <li className="nav-item">
            <a href="#submenu2-m" data-toggle="collapse" aria-expanded="false">
              Property
            </a>
            <ul className="collapse list-unstyled" id="submenu2-m">
              <li>
                <a href="property-condo.html">All Property</a>
              </li>
              <li>
                <a href="property-condo.html">Condo</a>
              </li>
              <li>
                <a href="property-apartment.html">Apartment</a>
              </li>
              <li>
                <a href="property-bungalow.html">Bungalow</a>
              </li>
              <li>
                <a href="property-terrace.html">Terrace</a>
              </li>
              <li>
                <a href="property-residential.html">Residential Land</a>
              </li>
            </ul>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="contact.html">
              Contact Us
            </a>
          </li>
        </ul>
      </nav>
    )
  }
}



