import React, { Component } from 'react'
import { string } from 'prop-types'

// Link
import { Link } from 'react-router-dom'

export default class PropertyBanner extends Component {
  static propTypes = {
    link: string,
  }
  render() {
    const { link } = this.props
    return (
      <section id="sec-banner">
        <div className="container">
          <div className="cls-banner-wrapper text-center">
            <h2 className="cls-header2">All Property</h2>
            <div className="cls-breadcrumb">
              <ul className="cls-breadcrumb-list">
                <li>
                  <Link to="/">
                    <i className="fas fa-home" />{' '}
                  </Link>
                </li>
                /
                <li>
                  <Link to="/all-property"> All Property</Link>
                </li>
                <li>
                  <Link to={link}> Detail</Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </section>
    )
  }
}



