import React, { Component } from 'react'

// Components
import {
  ContactBanner,
  ContactDetails,
  ContactMap,
  ContactForm,
} from './contactComponents'

export default class Contact extends Component {
  render() {
    return (
      <div>
        {/** Contact Banner **/}
        <ContactBanner />
        {/** Contact Details **/}
        <ContactDetails />
        {/** Google Map **/}
        <ContactMap />
        {/** Contact Form **/}
        <ContactForm />
      </div>
    )
  }
}



