import React, { Component } from 'react'

// Components
import {
  HomeSlide,
  HomeSearch,
  HomeAdvantages,
  HomeBookAppointment,
  HomeFeatured,
  HomeUpdate,
} from './homeComponents'

export default class Home extends Component {
  render() {
    return (
      <div>
        {/** Slide Show **/}
        <HomeSlide />
        {/** Search Component **/}
        {/*<HomeSearch />*/}
        {/** Featured Component **/}
        <HomeFeatured />
        {/** Advantages **/}
        <HomeAdvantages />
        {/** Subscribe **/}
        <HomeUpdate />
        {/** Book Appointment **/}
        <HomeBookAppointment />
      </div>
    )
  }
}



