import React, { Component } from 'react'
import { object } from 'prop-types'

export default class Overview extends Component {
  static propTypes = {
    overview: object,
  }
  render() {
    const { overview } = this.props
    return (
      <div className="cls-overview-outer mb-4">
        <h3 className="cls-header3 mb-4">Overview</h3>
        <p className="cls-overview">{overview.content}</p>
        <ul>{overview.list.map((item, i) => <li key={i}>{item}</li>)}</ul>
        <div className="row">
          <div className="col-12 col-sm-4 mb-2">
            <div className="cls-icon">
              <span className="cls-txt-icon">
                <i className="fa fa-building fa-2x" aria-hidden="true" />
              </span>{' '}
              {overview.level} Levels
            </div>
          </div>

          <div className="col-12 col-sm-4 mb-2">
            <div className="cls-icon">
              <span className="cls-txt-icon">
                <i className="fa fa-object-group fa-2x" aria-hidden="true" />
              </span>{' '}
              {overview.sqft} Sqft
            </div>
          </div>

          <div className="col-12 col-sm-4 mb-2">
            <div className="cls-icon">
              <span className="cls-txt-icon">
                <i className="fa fa-bed fa-2x" aria-hidden="true" />{' '}
              </span>{' '}
              {overview.rooms} Bed Rooms
            </div>
          </div>

          <div className="col-12 col-sm-4 mb-2">
            <div className="cls-icon">
              <span className="cls-txt-icon">
                <i className="fa fa-bath fa-2x" aria-hidden="true" />{' '}
              </span>{' '}
              {overview.bathroom} Baths
            </div>
          </div>

          <div className="col-12 col-sm-4 mb-2">
            <div className="cls-icon">
              <span className="cls-txt-icon">
                <i className="fa fa-car fa-2x" aria-hidden="true" />{' '}
              </span>{' '}
              {overview.parking} Parking
            </div>
          </div>
        </div>
      </div>
    )
  }
}



