import React, { Component } from 'react'

// Const
import { contactDetails } from '../../../data/const'

export default class ContactDetails extends Component {
  render() {
    return (
      <section id="sec-contactinfo">
        <div className="container">
          <div className="row row-eq-height">
            {contactDetails.map((item, i) => (
              <div className="col-12 col-sm-4" key={i}>
                <div className="cls-info-wrapper">
                  <div className="cls-info-content-wrapper">
                    <div className="cls-info-icon">
                      <span className="cls-txt-icon">
                        <i
                          className="fa fa-map-marker-alt fa-2x"
                          aria-hidden="true"
                        />
                      </span>
                    </div>

                    <h3 className="cls-header3 mt-3">{item.title}</h3>
                    <div className="cls-txt-address">
                      {i === 0
                        ? item.content[0]
                        : item.content.map((val, i) => (
                            <a href={item.link[i]} key={i}>
                              {val}
                              <br />
                            </a>
                          ))}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    )
  }
}



